$(document).ready(function() {

    // Alert
    $('.alert').click(function () {
        $('.settings').addClass('settings--active')
    })

    // SettingsBtn
    $('.settings__btn').click(function () {
        $(this).addClass('settings__btn--active')
        $('.transactions').addClass('transactions--active')
        $('.settings__form').addClass('settings__form--active')
        $('.configs').removeClass('configs--active')
    })

    $('.title-btn').click(function () {
        $('.settings__btn').removeClass('settings__btn--active')
        $('.configs').addClass('configs--active')
        $('.settings__form').removeClass('settings__form--active')
    })

    $('.title-btn2').click(function () {
        $('.settings__btn').removeClass('settings__btn--active')
        $('.transactions').removeClass('transactions--active')
        $('.configs').removeClass('configs--active')
    })

    $('.settings__edit').click(function () {
        $('.settings__form').addClass('settings__form--active')
        $('.configs').removeClass('configs--active')
        $('.settings__btn').addClass('settings__btn--active')
    })

    // Select
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list li').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list li").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list li')
    let selectWrap = $('.select__wrap')

    $(document).mouseover(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

})